.home-page {
  display: flex;
  flex-direction: column;
  max-width: 100vw;
}

.hero-image {
  /*overflow-x: hidden;*/
  object-fit: cover;
  object-position: 100% 55%;
  width: 100%;
  height: 300px;
  flex-shrink: 0;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  transition: transform 0.5s ease;
  max-width: 100vw;
}

.hero-image:hover {
  max-width: 100vw;
  transform: scale(1.05);
  overflow: hidden;

}


.home-image-container {
  z-index: 0;
  position: relative;
  overflow-x: clip;
}

.shop-now-btn {
  width: max-content;
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  top: 50%;
}

.about-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  grid-gap: 5rem;
  align-items: center;
  padding: 50px 1rem;
}

.category-container {
  padding: 20px;
  background: var(--neutral-color);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
  grid-gap: 5rem;
  max-width: 100vw;
  text-wrap: pretty;
}

.category-text {
  min-width: 40%;
  max-width: inherit;
}

.category-title {
  font-size: 20px;
  max-width: inherit;}

.category-description {
  font-size: medium;
  max-width: inherit;
}

.category-img-container {
  padding: 20px;
  min-width: 60%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(90px, 1fr));
  grid-gap: 1rem;
  color: var(--neutral-color);
  max-width: inherit;
}

.category-item {
  padding: 10px;
  background-color: var(--primary-color);
  color: var(--neutral-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  cursor: pointer;

}

.category-item:hover,
.category-item:active,
.category-item:visited {
  background-color: color-mix(in HSL, var(--primary-color) 80%, transparent) !important;
  color: var(--neutral-color);
  transition: 0.4s;
  text-decoration: none;
}

.category-img {
  font-size: 40px;
  padding: 10px;
}

.ampersand-logo {
  color: var(--neutral-color);
  width: max-content;
  position: absolute;
  transform: translate(-50%);
  left: 50%;
  font-size: 70px;
  bottom: -30px;
}

input[type=text] {
  width: 100%;
  box-sizing: border-box;
  border: 0;
  border-radius: var(--default-border-radius);
  font-size: 16px;
  background-color: var(--neutral-color);
}


.about-card {
  min-width: 40%;
  padding: 40px;
  color: white;
  border: 1px solid #7F534B;
  background: #7F534B;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25) inset;
  height: inherit;
  display: flex;
  justify-items: center;
  max-width: 100vw;
}

.about-card img {
  object-fit: cover;
  object-position: 100% 55%;
  height: 200px;
  flex-shrink: 0;
}

.about-card-container {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
}

.about-text-container {
  padding: 20px 20px;
  display: flex;
  flex-direction: column;
}

.about-text {
  padding-top: 30px;
}