main {
  display: flex;
  flex-direction: column;
}

root{
  overflow-x: hidden;
}

.hollow {
  font-family: var(--title-font-family), serif;
  font-weight: 100;
}

.numberCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 2px;
  right: 15%;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  padding: 0;

  background: var(--neutral-color);
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
  text-align: center;

  font-size: 11px;

}

.numberCircle-Container {
  position: relative;
  text-align: center;
}

.number {
  font-family: Arial, sans-serif;
}

.center{
  text-align: center !important;
}

.end{
  text-align: end !important;
}