.cartTitle {
    font-size: 50px;
    font-weight: 150;
    padding-top: 10px;
    color: white;
    text-align: center;
}

.cart-action-btns {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

button.cart-action-button {
    margin: 0 10px 10px 10px;
    border: black 0;
    color: white;
}

button.clear-button {
    border: 0;
    color: black;
    align-self: end;
}

.cart-table-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: white;
}

.cart-table-container-parent {
    display: flex;
    justify-content: center;
}

.cart-header-icon {
    padding-right: 10px;
}

.emptyCart {
    font-weight: bold;
    text-align: center;
    margin: 30px 0 0 0;
    color: white;
}

.cart-contents {
    color: white;
    text-align: center;
    padding-top: 20px
}

.cart-table {
    display: grid;
    grid-template-columns: max-content minmax(10em, 20em) repeat(4, max-content);
    row-gap: 1em;
    width: fit-content;
    background-color: white;
    margin: 0 auto 10px auto;
    padding-bottom: 20px;
    position: relative;
    border-bottom: black 1px solid;
}

.removeFromCartText {
    padding-top: 10px;
    color: blue;
    cursor: pointer;
    font-size: 0.75em;
}

ul.cartList {
    display: contents;
}

ul.cartList > li {
    display: contents;
}

.table-heading {
    background-color: var(--primary-color);
    color: white;
}

.table-heading > * {
    background-color: var(--primary-color);
    padding: 0.75em;
}

.heading-book {
    grid-column: 1 / 3;
}

.heading-price {
    grid-column: 3 / 4;
    text-align: center;
}

.heading-quantity {
    grid-column: 4 / 5;
    text-align: center;
}

.heading-subtotal {
    text-align: right;
    grid-column: 5 / 6;

}

.cart-book-image {
    grid-column: 1 / 2;
    padding: 0 1em;
}

.cart-book-image > * {
    margin-left: auto;
    margin-right: 0;
}

.cart2 img {
    display: block;
    width: 100px;
    height: auto;
}

.rect {
    background-color: var(--primary-color-dark);
}

.narrow-rect {
    width: 75px;
}

.square {
    width: 100px;
    height: 100px;
}

.wide-rect {
    width: 125px;
    height: 100px;
}

.cart-book-price {
    padding-left: 1em;
    text-align: right;
}

.cart-book-quantity {
    padding-left: 1em;
    padding-right: 1em;
}

.cart-book-subtotal {
    text-align: right;
    padding-left: 1em;
    padding-right: 1em;
}

/* Row separators in the table */

.line-sep {
    display: block;
    height: 2px;
    background-color: gray;
    grid-column: 1 / -1;
}

/* Increment/decrement buttons */

.icon-button {
    margin: 5px 0 5px 0;
    border: none;
    cursor: pointer;
}

.inc-button {
    font-size: 1.125rem;
    color: var(--primary-color);
    margin-right: 0.25em;
    background-color: transparent;
}

.inc-button:hover {
    color: var(--primary-color-dark);
}

.dec-button {
    font-size: 1.125rem;
    color: #ccc;
    margin-left: 0.25em;
    background-color: transparent;
}

.dec-button:hover {
    color: #aaa;
}

.row-line {
    border-bottom: 1px solid black;
}

.checkout-button-container {
    padding-top: 5px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-bottom: 20px;
    font-size: 20px;
    text-align: center;
}

.checkout-button {
    max-height: none;
}

button.disabled {
    background-color: gray;
    color: black;
    border: 1px solid black;
    cursor: default;
}

button.disabled:hover,
button.disabled:active {
    background-color: gray;
    color: black;
    border: 1px solid black;
    cursor: default;
}