.confirmationView {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1em auto;
}

ul {
    text-align: left;
}

ul > li {
    margin: .5em;
}

.confirmation_table {
    border: 1px black solid;
    width: fit-content;
    margin-top: 1em;
    margin-bottom: 1em;
}

.confirmation_td {
    display: table-cell;
    padding: 0.5em 0.5em;
    background-color: white;
    vertical-align: middle;
}

.confirmation_tr:nth-child(even) > td {
    background-color: lightgray;
}

.confirmation_td:nth-child(1) {
    text-align: left;
}

.confirmation_td:nth-child(2) {
    text-align: center;
}

.confirmation_td:nth-child(3) {
    text-align: right;
}

.checkout-text {
    margin-top: 40px;
    margin-left: 50px;
    margin-bottom: 20px;
}

.checkout-view {
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 10px;
}

.confirmation-container {
    padding: 20px;
    text-align: center;
    width: 100%;
    font-family: Arial, sans-serif;
}

.confirmation-view {
    background-color: #f8f9fa;
    border-radius: 8px;
    padding: 30px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.confirmation-thankyou {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
}

.confirmation-details {
    list-style-type: none;
    padding: 0;
    margin: 20px 0;
    font-size: 16px;
    color: #444;
}

.customer-info {
    list-style-type: none;
    padding: 0;
    font-size: 16px;
    color: #444;
    line-height: 1.8;
}

.confirmation-error {
    color: #d9534f;
    font-size: 18px;
}

.confirmation-layout {
    margin-top: 15px;
    border-top: 1px solid black;
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    gap: 20px;
    justify-content: space-between;
    align-items: flex-start;
}

.confirmation-center {
    text-align: center;
}

.confirmation-left {
    flex: 1;
    min-width: 300px;
}

.confirmation-right {
    flex: 1;
    min-width: 300px;
    text-align: left;
}

.confirmation_table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.confirmation_table th, .confirmation_table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.confirmation_table th {
    background-color: #f2f2f2;
    font-weight: bold;
}

@media (max-width: 768px) {
    .confirmation-layout {
        flex-direction: column;
    }

    .confirmation-left, .confirmation-right {
        width: 100%;
    }
}


.table-total-row{
    border-top-style: double;
    border-top-color: black;
    border-top-width: 3px;
}