.empty-checkout-cart-container {
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
}

.empty-checkout-cart-text {
    color: white;
    font-size: 1.5em;
    padding-bottom: 10px;
}

.empty-checkout-cart-button {
    justify-self: center;
    max-width: fit-content;
}

.checkout-summary-box {
    width: 100%;
    height: auto;
    background-color: white;
    color: black;
    border-radius: var(--default-border-radius);
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.checkout-summary {
    padding-bottom: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid black;
}

.checkout-table {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-row-gap: 5px;
    column-gap: 10px;
}

.checkout-cell {
    display: block;
    align-items: center;
}

.checkout-left {
    text-align: right;
}

.checkout-right {
    text-align: right;
}


.checkout-cart-table-view {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-content: space-around;
    justify-content: center;
    padding: 20px 40px 20px 40px;
}

.checkout-cart-table {
    display: flex;
    flex-direction: column;
}

.checkout-cart-info {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    column-gap: 1em;
    row-gap: 1em;
    padding: 10px;
    box-sizing: border-box;
}


.checkout-display {
    margin-top: 80px;
    width: 300px;
    height: 250px;
    border: 1px solid black;
    border-radius: var(--default-border-radius);
}

.checkout-text {
    text-align: center;
    width: 80%;
    min-width: 200px;
}

.checkout-text-line {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.checkout-btn-center {
    text-align: center;
}

.checkout-text-left {
    text-align: start;
}

.checkout-text-left {
    text-align: end;
}

.checkout-page-body {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-height: 90vh;
    border-radius: var(--default-border-radius);
    padding: 1em;
    gap: 70px;
    position: sticky;
}

.checkout-cart-info-ul {
    display: contents;
}

.checkout-cart-info-ul > li {
    display: contents;
}

.checkout-table-heading {
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
}

.checkout-table-heading > * {
    background-color: rgba(79, 203, 242, 0.32);
    padding: 0.5em;
}

.checkout-heading-book {
    grid-column: 1 / 3;
}

.checkout-cart-book-title {
    font-size: 1.25em;
    padding-bottom: 15px;
    flex-grow: 1;
    max-width: 250px;
}

.checkout-heading-price {
    grid-column: 3 / 5;
    text-align: right;
}

.checkout-heading-subtotal {
    text-align: right;
    grid-column: -2 / -1;
}

.checkout-cart-book-image {
    width: 150px;
    height: 200px;
}

.checkout-cart-book-info {
    margin-left: auto;
    margin-right: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-items: right;
    height: inherit;
    width: calc(100% - 150px);
    overflow-x: hidden;
}


.checkout-cart-book-item {
    color: black;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow-x: hidden;
    min-width: 320px;
    height: 220px;
    border-radius: var(--default-border-radius);
    padding: 10px;
    background-color: var(--neutral-color);
    max-width: 500px;
}


.checkout-cart-info-img {
    width: 90%;
    height: 90%;
}

.checkout-row-line {
    border-bottom: 1px solid black;
}

.checkout-num-button {
    color: black;
}

.checkout-narrow-rect {
    width: 75px;
    height: 100px;
}

.checkout-square {
    width: 100px;
    height: 100px;
}

.checkout-wide-rect {
    width: 125px;
    height: 100px;
}

.checkout-cart-book-price {
    padding-left: 1em;
    text-align: right;
}

.checkout-cart-book-quantity {
    display: flex;
    text-align: end;
    justify-content: flex-end;
    align-items: center;
}

.checkout-cart-book-subtotal {
    padding: 0.5em 0 0 0;
    display: flex;
    text-align: end;
    justify-content: flex-end;
    align-items: flex-end;
}

/* Row separators in the table */

.checkout-btn-primary {
    font-family: "Montserrat Alternates";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    height: 25px;
    width: 200px;
    background-color: #1510f5;
    color: white;
    border: 1px solid #555;
    margin-top: 20px;
    margin-right: 50px;
    border-radius: var(--default-border-radius);
}

.checkout-btn-secondary {
    font-family: "Montserrat Alternates";
    font-style: normal;
    height: 25px;
    width: 200px;
    font-size: 20px;
    margin-left: 30px;
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
    border: 1px solid #555;
    margin-top: 10px;
    border-radius: var(--default-border-radius);
}

.checkout-btn-ter {
    font-family: "Montserrat Alternates";
    font-style: normal;
    height: 20px;
    width: 100px;
    font-size: 15px;
    margin-left: 20px;
    color: #1510f5;
    background-color: white;
    border: none;
    margin-top: 10px;
    border-radius: var(--default-border-radius);
}

/* Increment/decrement buttons */

.checkout-icon-button {
    border: none;
    cursor: pointer;
}

.checkout-inc-button {
    transition: 0.3s;
    background-color: transparent;
    font-size: 1.125rem;
    color: var(--primary-background-color);
    margin-right: 0.25em;
}

.checkout-inc-button:hover {
    color: var(--card-background-color);
}

.checkout-dec-button {
    transition: 0.3s;
    background-color: transparent;
    font-size: 1.125rem;
    color: #aaa;
}

.checkout-dec-button:hover {
    color: black;
}

/* Chevron buttons */

.checkout-dec-arrow-button,
.checkout-inc-arrow-button {
    font-size: 1rem;
    color: var(--card-background-color);
}

.checkout-dec-arrow-button:hover,
.checkout-inc-arrow-button:hover {
    color: var(--card-background-color);
}

input[type="number"] {
    width: 4em;
}

.checkout-select {
    background-color: rgba(79, 203, 242, 0.32);
    color: black;
    border: 2px solid var(--primary-color-dark);
    border-radius: var(--default-border-radius);
}

form.category-form {
    color: black;
    display: flex;
    flex-direction: column;
    border-radius: var(--default-border-radius);
    padding: 20px;
    min-height: 360px;
    background-color: white;
}

form.category-form > div {
    display: flex;
    justify-content: flex-end;
    margin-top: 0.5em;
    align-items: center;
}

form.category-form > div:first-child {
    margin-top: 0;
}

form.category-form > div > input,
form.category-form > div > select,
form.category-form > div > input :-webkit-autofill,
form.category-form > div > select:-webkit-autofill {
    background-color: var(--neutral-color);
    margin-left: 0.5em;
    color: black;
    align-items: center;
}

form.category-form .error {
    text-align: right;
    font-weight: 700 !important;
    font-size: 0.65em;
    color: #cc0033;
    display: inline-block;
    line-height: 12px;
    margin: 5px 0 10px;
}

.checkoutStatusBox {
    margin: 1em;
    padding: 1em;
    text-align: center;
}

.checkout-view {
    dsiplay: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: var(--default-border-radius);
}

