footer {
  background: var(--secondary-background-color);
  display: flex;
  gap: 1em;
  overflow: auto;
}

.links,
.social-media-icons {
  display: flex;
  flex-direction: column;
}

.footer-info {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 20px;
  flex-grow: 1;
}

.social-media-icons {
  text-align: center;
  gap: 0.25em;
}

.social-button,
.social-button:active,
.social-button:visited,
.social-button:hover{
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  align-items: center;
  color: var(--neutral-color);
}

.copyright-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-logo {
  font-size: 40px;
  color: white;
}

.copyright-text {
  color: var(--neutral-color);
  font-size: small;
}

.address {
  color: var(--neutral-color);
  font-size: small;
  text-decoration: underline;
  cursor: pointer;
}

.address:hover,
.address:active,
.address:visited {
  color: var(--neutral-color);
  font-size: small;
}

.footer-directory {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.footer-directory-item,
.footer-directory-item:hover,
.footer-directory-item:active,
.footer-directory-item:visited {
  color: var(--neutral-color);
  text-decoration: underline;
  cursor: pointer;
}