.dropdown {
    position: relative;
    display: inline-block;
    color: var(--neutral-color);
}

.dropdown-content {
    display: block;
    position: absolute;
    background-color: var(--primary-color);
    min-width: 160px;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 1;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-out;
}

.dropdown:hover .dropdown-content {
    max-height: 500px;
}

.dropdown li + li {
    border-top: 1px solid var(--neutral-color);
}

.dropdown-item {
    color: var(--neutral-color);
    padding: 12px 10px;
}

.dropdown-item:hover {
    color: var(--neutral-color);
    transition: ease 0.4s;
    background-color: color-mix(in HSL, var(--neutral-color) 20%, transparent);
}

.dropdown-item-text,
.dropdown-item-text:visited,
.dropdown-item-text:active,
.dropdown-item-text:hover {
    text-decoration: none;
    padding: 12px 16px;
    color: var(--neutral-color);
    width: max-content;
}
