.book-box {
    display: flex;
    flex-direction: row;
    background-color: var(--neutral-color);
    padding: 1em;
    gap: 0.25em;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    border-radius: var(--default-border-radius);
    max-width: max-content;
}

.book-text-area {
    width: fit-content;
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 10px;
}

.book-title {
    font-size: 25px;
    font-weight: 700;
}

.book-author {
    font-style: italic;
    font-weight: 500;
    font-size: 15px;
}

.book-description {
    flex-grow: 1;
    font-size: 12px;
    padding-top: 10px;
}

.price {
    text-align: end;
}

.add-to-cart-btn {
    align-self: flex-end;
    width: fit-content;
    border-radius: var(--default-border-radius);
    border: 1px;
}

.book-img-container {
    max-width: 500px;
    position: relative;
    z-index: 1;
}

.book-img {
    z-index: -1;
    object-fit: cover;
    width: 150px;
    height: 200px;
    overflow: hidden;
    border-radius: var(--default-border-radius);
}

.read-now-btn {
    position: absolute;
    left: 5px;
    right: 5px;
    bottom: 5px;
    font-size: small;
    text-align: center;
    background-color: var(--primary-background-color);
}