header {
    background: var(--secondary-background-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    gap: 1em;
    z-index: 2;
    width: 100%;
}

.header-left {
    display: flex;
    padding: 1em;
    align-items: center;
}

.header-left img {
    height: 40px;
    margin-right: 20px;
}

.header-center-logo {
    display: flex;
    align-items: center;
    color: #D6D1B1;
    font-size: 1em;
    text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    font-style: normal;
    font-weight: 100;
    line-height: normal;
    flex-direction: column;
}

.header-center-logo a,
.header-center-logo a:visited,
.header-center-logo a:active,
.header-center-logo a:hover {
    text-decoration: none;
    color: var(--neutral-color);
}

.logo-subtext {
    margin: 0;
    text-align: center;
    font-size: 20px;
    font-weight: 50;
}

.search-bar {
    border-radius: var(--default-border-radius);
    background-image: url('../../images/icons/image.png');
    background-position: 10px 10px;
    background-repeat: no-repeat;
    background-size: 25px;
    padding: 12px 20px 12px 50px;
}

.header-action-category {
    display: flex;
    padding: 1em;
    gap: 10px;
    align-self: flex-start;
}

@media (max-width: 600px) {
    header {
        flex-direction: column;
    }
}

.header-icon-button {
    display: flex;
    align-items: center;
    justify-content: center;
}